.form {
  background: #F6F6F6;
  padding: 160px 0 115px;
  @include r($tablet) {
    padding: 60px 0; }
  @include r($mobile) {
    padding: 40px 0; }
  form {
    background: #222222;
    border-radius: 18px;
    padding: 60px 50px;
    display: flex;
    flex-direction: column;
    input, textarea {
      background: transparent;
      padding: 20px 0;
      color: white;
      border-bottom: 2px solid #5F5F5F;
      margin-bottom: 20px;
      &::placeholder {
        color: white; } } }
  &__block {
    margin-left: 60px;
    @include r($mobile) {
      margin-left: 0;
      margin-top: 20px; }
    h2 {
      margin-bottom: 42px;
      font-size: 33px;
      @include r($mobile) {
        margin-bottom: 20px; } }
    p {
      margin-bottom: 35px; }
    .row {}
    &__phone {
      margin-right: 20px;
      display: flex;
      align-items: center;
      @include r($tablet) {
        justify-content: center;
        width: 50%;
        margin-bottom: 20px; }
      i {
        font-size: 16px;
        margin-right: 13px; }
      a {
        font-size: 14px;
        text-decoration: none;
        color: black;
        &:hover {
          text-decoration: underline; } } }
    &__address {
      display: flex;
      align-items: center;
      i {
        font-size: 16px;
        margin-right: 13px; }
      span {
        font-size: 14px; } } } }
.modal-form {
  padding: 0;
  form {
    background: white;
    input, textarea {
      color: #929292;
      border: none;
      border-bottom: 2px solid #5F5F5F;
      &::placeholder {
        color: #929292; } }
    .btn {
      background: black;
      color: white;
      border: 1px solid white;
      &:hover {
        background: white;
        color: black;
        border: 1px solid black; } } } }

