.about {
  padding: 110px 0;
  background: #F6F6F6;
  @include r($tablet) {
    padding: 60px 0; }
  &__image {
    width: 100%;
    img {
      width: 100%; } }
  h2 {
    margin-left: 60px;
    margin-bottom: 45px;
    @include r($tablet) {
      margin-left: 0;
      margin-top: 30px;
      margin-bottom: 20px; }
    @include r($mobile) {
      font-size: 28px; } }

  p {
    margin-left: 60px;
    font-size: 16px;
    line-height: 25px;
    color: #595959;
    @include r($tablet) {
      margin-left: 0; } } }
