.slideout-menu {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 256px;
  min-height: 100vh;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  z-index: 0;
  display: none;
  ul {
    list-style: none;
    li {
      a {
        text-decoration: none;
        color: black;
        padding: 10px 30px;
        display: block; }
      &.current-item {
        a {
          text-decoration: underline; } } } }
  .header {
    &__info__calc {
        color: black;
        margin-bottom: 20px; }
    &__toolbar__phone a {
        color: black;
        margin-bottom: 20px;
        margin-top: 20px; } }
  .btn {
    color: black !important;
    display: inline-block;
    margin-top: 0; }
  .center {
    display: flex;
    flex-direction: column;
    align-items: center; } }

.slideout-menu-left {
  left: 0; }

.slideout-menu-right {
  right: 0; }

.slideout-panel {
  position: relative;
  z-index: 1;
  will-change: transform;
  background-color: #FFF;
  min-height: 100vh; }

.slideout-open {
  overflow: hidden;
  body, .slideout-panel {
    overflow: hidden; }
  .slideout-menu {
    display: block; } }


.header__menu {
  align-items: center;
  cursor: pointer;
  display: flex;
  font-size: 0;
  flex-shrink: 0;
  justify-content: center;
  position: relative;
  z-index: 15;
  width: auto;
  min-width: 30px;
  height: 40px;
  span {
    display: block;
    height: 3px;
    transition-duration: .25s;
    transition-property: background-color, transform;
    &:after, &:before {
      display: block;
      height: 3px;
      transition-duration: .25s;
      transition-property: background-color, transform; }
    background: #FFF;
    width: 30px;
    &:after {
      background-color: #FFF;
      content: "";
      position: absolute;
      width: inherit; }
    &:before {
      background-color: #FFF;
      content: "";
      position: absolute;
      width: inherit;
      -ms-transform: translateY(-10px);
      transform: translateY(-10px); }
    &:after {
      -ms-transform: translateY(10px);
      transform: translateY(10px); } }
  &.is-active span {
    animation: burgerCloseA .3s;
    animation-fill-mode: forwards;
    &:after, &:before {
      animation: burgerCloseB .3s;
      animation-fill-mode: forwards; } }
  &.is-closing span {
    animation: burgerOpenA .3s;
    animation-fill-mode: forwards;
    &:after, &:before {
      animation: burgerOpenB .3s;
      animation-fill-mode: forwards; } } }

@keyframes burgerCloseA {
  50% {
    transform: translateY(0); }

  100% {
    transform: translateY(0) rotate(-45deg); } }


@keyframes burgerCloseB {
  50% {
    transform: translateY(0); }

  100% {
    transform: translateY(0) rotate(90deg); } }


@keyframes burgerOpenA {
  0% {
    transform: translateY(0) rotate(-45deg); }

  50% {
    transform: translateY(0); } }


@keyframes burgerOpenB {
  0% {
    transform: translateY(0) rotate(90deg); }

  50% {
    transform: translateY(0); } }


.overlay-layout {
  display: none;
  flex-direction: column;
  left: 0;
  opacity: 0;
  position: absolute;
  top: 0;
  transition: opacity 300ms;
  visibility: hidden;
  width: 100%;
  min-height: 100%;
  z-index: 999;
  background-color: #000;
  padding-top: 200px;
  body.modal-open {
    display: flex;
    opacity: 1;
    visibility: visible; } }

.overlay-container {
  width: 100%; }
