.pagination__wrapper { margin-top: 50px; background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, white 17%, white 83%, rgba(255, 255, 255, 0) 100%); filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#00ffffff',GradientType=1 ); height: 50px; text-align: center; }

.pagination__wrapper:before, .pagination__wrapper:after { background: linear-gradient(to right, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.1) 17%, rgba(0, 0, 0, 0.1) 83%, rgba(0, 0, 0, 0) 100%); filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#00000000',GradientType=1 ); content: ""; height: 1px; left: 0; position: absolute; width: 100%; }

.pagination__wrapper:before { top: -1px; }

.pagination__wrapper:after { bottom: -1px; }

@-webkit-keyframes hoverAnimation { from { opacity: 1; }
  to { opacity: 0; } }

@keyframes hoverAnimation { from { opacity: 1; }
  to { opacity: 0; } }

.pagination { display: inline-block; list-style: none; margin: 0; padding: 0; }

.pagination li { display: block; float: left; padding: 5px; }

.pagination li:first-child { border: none; }

.pagination button, .pagination span { background: none; border: none; border-radius: 50%; box-sizing: border-box; color: rgba(0, 0, 0, 0.6); display: block; font-size: 16px; height: 40px; line-height: 40px; min-width: 40px; padding: 0; }

.pagination button { outline: none; position: relative; transition: all 170ms linear; }

.pagination button:before { background: rgba(0, 0, 0, 0.2); border-radius: 50%; content: ""; cursor: pointer; height: 0; left: 50%; opacity: 0; position: absolute; -webkit-transform: translate(-50%, -50%); transform: translate(-50%, -50%); transition: all 170ms linear; top: 50%; width: 0; }

.pagination button:hover:not(.active) { color: black; }

.pagination button:hover:not(.active):before { -webkit-animation: hoverAnimation 510ms linear forwards; animation: hoverAnimation 510ms linear forwards; height: 40px; width: 40px; }

.pagination button.active { background: rgba(0, 0, 0, 0.1); color: black; }

.pagination .prev, .pagination .next { font-size: 14px; }

.project { margin-top: 30px; }

.project__text { background: #F6F6F6; padding: 43px; margin-bottom: 20px; }

.project__text ul { list-style: circle; }

.project__image { width: 100%; margin-bottom: 20px; }

.project__image img { width: 100%; display: block; -o-object-fit: cover; object-fit: cover; font-family: "object-fit: cover"; }

.components-documents { padding-top: 85px; padding-bottom: 60px; }

.components-documents .row { justify-content: space-between; }

.components-documents__block { cursor: pointer; display: flex; flex-direction: column; align-items: center; width: 16.6%; background: #fff; margin-bottom: 30px; padding: 30px; border-radius: 10px; }

@media only screen and (max-width: 991px) { .components-documents__block { width: 33.33%; } }

@media only screen and (max-width: 767px) { .components-documents__block { width: 50%; } }

.components-documents__block img { width: 60px; height: 60px; margin-bottom: 20px; }

.components-documents__block h3 { font-family: "PT Sans"; font-weight: normal; font-size: 18px; text-align: center; margin-bottom: 35px; }

.components-documents__block:hover { background: #F6F6F6; }

.components-documents__block:hover .btn { background: black; color: white; }

.contact__info { background: #E9E9E9; padding: 65px 0; display: flex; justify-content: center; }

@media only screen and (max-width: 991px) { .contact__info { justify-content: flex-start; } }

@media only screen and (max-width: 767px) { .contact__info { flex-direction: column; align-items: center; padding: 40px 0; } }

.contact__info__block { display: flex; }

.contact__info__block:nth-of-type(2) i { font-size: 55px; }

.contact__info__block:nth-of-type(3) i { font-size: 53px; }

@media only screen and (max-width: 991px) { .contact__info__block { width: 33%; justify-content: center; } }

@media only screen and (max-width: 767px) { .contact__info__block { width: 70%; justify-content: center; margin-bottom: 20px; } }

.contact__info__block:not(:last-child) { margin-right: 70px; }

@media only screen and (max-width: 991px) { .contact__info__block:not(:last-child) { margin-right: 0px; } }

.contact__info__block i { color: #CACACA; font-size: 50px; }

@media only screen and (max-width: 767px) { .contact__info__block i { width: 30%; } }

.contact__info__block div { margin-left: 30px; }

@media only screen and (max-width: 991px) { .contact__info__block div { margin-left: 10px; } }

@media only screen and (max-width: 767px) { .contact__info__block div { width: 70%; } }

.contact__info__block div p { font-size: 14px; font-weight: bold; color: black; margin-bottom: 10px; }

.contact__info__block div span { opacity: 0.6; }

.contact__info__block div a { color: black; text-decoration: none; }
