.projects {
  padding-top: 30px;
  padding-bottom: 60px;
  &__block {
    width: 50%;
    display: block;
    text-decoration: none;
    @include r($mobile) {
      width: 100%; }
    article {
      height: 380px;
      width: 100%;
      position: relative;
      @include r($mobile) {
        height: 300px; }
      img {
        width: 100%;
        height: 100%;
        @include object-fit(cover);
        display: block; }
      h1 {
        font-size: 18px;
        color: white;
        position: absolute;
        bottom: 80px;
        left: 30px;
        opacity: 0;
        transition: all 0.3s;
        @include r($tablet) {
          opacity: 1; }
        @include r($mobile) {
          bottom: 90px; } }
      p {
        font-size: 12px;
        color: white;
        position: absolute;
        bottom: 40px;
        left: 30px;
        opacity: 0;
        transition: all 0.3s;
        line-height: normal;
        @include r($tablet) {
          opacity: 1; }
        @include r($mobile) {
          bottom: 20px; } }
      &:before {
        content: '';
        background: rgba(0, 0, 0, 0.65);
        width: 100%;
        height: 100%;
        position: absolute;
        opacity: 0;
        transition: all 0.3s;
        @include r($tablet) {
          opacity: 1; } }
      &:hover {
        h1, p, &:before {
          opacity: 1; } } } } }



.pagination__wrapper {
  margin-top: 50px;
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, white 17%, white 83%, rgba(255, 255, 255, 0) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#00ffffff',GradientType=1 );
  height: 50px;
  text-align: center;
  &:before, &:after {
    background: linear-gradient(to right, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.1) 17%, rgba(0, 0, 0, 0.1) 83%, rgba(0, 0, 0, 0) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#00000000',GradientType=1 );
    content: "";
    height: 1px;
    left: 0;
    position: absolute;
    width: 100%; }
  &:before {
    top: -1px; }
  &:after {
    bottom: -1px; } }

@-webkit-keyframes hoverAnimation {
  from {
    opacity: 1; }

  to {
    opacity: 0; } }


@keyframes hoverAnimation {
  from {
    opacity: 1; }

  to {
    opacity: 0; } }


.pagination {
  display: inline-block;
  list-style: none;
  margin: 0;
  padding: 0;
  li {
    display: block;
    float: left;
    padding: 5px;
    &:first-child {
      border: none; } }
  button, span {
    background: none;
    border: none;
    border-radius: 50%;
    box-sizing: border-box;
    color: rgba(0, 0, 0, 0.6);
    display: block;
    font-size: 16px;
    height: 40px;
    line-height: 40px;
    min-width: 40px;
    padding: 0; }
  button {
    outline: none;
    position: relative;
    transition: all 170ms linear;
    &:before {
      background: rgba(0, 0, 0, 0.2);
      border-radius: 50%;
      content: "";
      cursor: pointer;
      height: 0;
      left: 50%;
      opacity: 0;
      position: absolute;
      -webkit-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
      transition: all 170ms linear;
      top: 50%;
      width: 0; }
    &:hover:not(.active) {
      color: black;
      &:before {
        -webkit-animation: hoverAnimation 510ms linear forwards;
        animation: hoverAnimation 510ms linear forwards;
        height: 40px;
        width: 40px; } }
    &.active {
      background: rgba(0, 0, 0, 0.1);
      color: black; } }
  .prev, .next {
    font-size: 14px; } }
