.header {
  &__bottom {
    height: 120px;
    @include r($mobile) {
      height: 100%;
      padding-bottom: 10px; } }
  &__menu {
    i {
      font-size: 20px;
      color: white;
      position: relative;
      top: 3px; } }
  &__toolbar {
    background: $blue;
    height: 45px;
    nav {
      ul {
        display: flex;
        justify-content: space-between;
        li.current-item {
          a {
            border-bottom: 1px solid #ffffff;
            font-weight: 600; } }
        a {
          color: white;
          text-decoration: none;
          font-size: 16px;
          font-weight: 500;
          &:hover {
            border-bottom: 1px solid #ffffff; } } } }

    &__city {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      i {
        font-size: 14px;
        color: white;
        margin-right: 12px; }
      a {
        text-decoration: none;
        color: white;
        font-size: 14px;
        &:hover {
          border-bottom: 1px solid #ffffff; } } }
    &__phone {
      a {
        color: white;
        font-size: 14px;
        text-decoration: none;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        span {
          &:hover {
            border-bottom: 1px solid #ffffff; } }
        i {
          margin-right: 12px; } } } }
  &__logo {
    width: 60px;
    text-decoration: none;
    display: flex;
    align-items: center;
    @include r($mobile) {
      padding: 20px 0; }
    span {
      color: black;
      font-family: $mont;
      font-size: 14px;
      font-weight: 500;
      text-transform: uppercase;
      padding-left: 35px;
      margin-top: 10px; } }
  &__info {
    &__calc {
      color: black;
      font-size: 16px;
      text-decoration: none;
      display: flex;
      align-items: center;
      @include r($mobile) {
        margin-bottom: 20px; }
      &:hover {
        span {
          text-decoration: underline; } }
      i {
        background: black;
        color: white;
        margin-right: 10px;
        font-size: 20px;
        text-decoration: none; } }
    &__social {
      display: flex;
      @include r($mobile) {
        justify-content: center;
        margin-bottom: 20px; }
      a {
        font-size: 16px;
        color: $blue;
        height: 17px;
        &:not(:last-child) {
          margin-right: 25px; } } } } }


